<template>
    <el-row>
        <basic-header :items="['商家管理','店铺管理']"></basic-header>
        <el-tabs v-model="activeName">
            <el-tab-pane label="已审核" name="audited">
                <el-form label-width="80px" :inline="true">
                    <el-form-item>
                        <el-input placeholder="请输入店铺名称" v-model="search1"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" icon="el-icon-search" circle @click="handleSearch1"></el-button>
                    </el-form-item>
                </el-form>
                <el-table :data="tableData1" :row-class-name="tableRowClassName">
                    <el-table-column type="expand">
                        <template #default="o">
                            <el-form>
                                <el-col :span="24">
                                    <el-form-item label="描述：">
                                        <span>{{o.row.description}}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="银行：">
                                        <span>{{o.row.bank}}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="银行卡号：">
                                        <span>{{o.row.bank_card}}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="持卡人：">
                                        <span>{{o.row.bank_person}}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="logo：">
                                        <img :src="o.row.logo" alt="" width="100">
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="身份证正面：">
                                        <img :src="o.row.idcard1" alt="" width="100">
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="身份证反面：">
                                        <img :src="o.row.idcard2" alt="" width="100">
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="营业执照：">
                                        <img :src="o.row.license" alt="" width="100">
                                    </el-form-item>
                                </el-col>
                                <el-col :span="24" v-if="o.row.quit===2">
                                    <el-form-item label="注销原因：">
                                        <span>{{o.row.reason}}</span>
                                    </el-form-item>
                                </el-col>
                            </el-form>
                        </template>
                    </el-table-column>
                    <el-table-column label="id" prop="id"></el-table-column>
                    <el-table-column label="企业名称" prop="name"></el-table-column>
                    <el-table-column label="所在城市" prop="city"></el-table-column>
                    <el-table-column label="操作">
                        <template #default="o">
                            <el-button type="text" v-if="o.row.quit===2" @click="quit(o.row.id)">允许注销</el-button>
                            <span v-else-if="o.row.quit===3">
                                已注销
                            </span>
                        </template>
                    </el-table-column>
                </el-table>
                <el-pagination
                        background
                        layout="prev,pager,next"
                        :current-page.sync="page1"
                        :page-size.sync="pageSize1"
                        :total="total1"
                        @current-change="handleChange1"
                        style="margin-top: 30px"></el-pagination>
            </el-tab-pane>
            <el-tab-pane label="未审核" name="notAudited">
                <el-form label-width="80px" :inline="true">
                    <el-form-item>
                        <el-input placeholder="请输入店铺名称" v-model="search2"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" icon="el-icon-search" circle @click="handleSearch2"></el-button>
                    </el-form-item>
                </el-form>
                <el-table :data="tableData2">
                    <el-table-column type="expand">
                        <template #default="o">
                            <el-form>
                                <el-col :span="24">
                                    <el-form-item label="描述：">
                                        <span>{{o.row.description}}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="银行：">
                                        <span>{{o.row.bank}}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="银行卡号：">
                                        <span>{{o.row.bank_card}}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="持卡人：">
                                        <span>{{o.row.bank_person}}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="logo：">
                                        <img :src="o.row.logo" alt="" width="100">
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="身份证正面：">
                                        <img :src="o.row.idcard1" alt="" width="100">
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="身份证反面：">
                                        <img :src="o.row.idcard2" alt="" width="100">
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="营业执照：">
                                        <img :src="o.row.license" alt="" width="100">
                                    </el-form-item>
                                </el-col>
                            </el-form>
                        </template>
                    </el-table-column>
                    <el-table-column label="id" prop="id"></el-table-column>
                    <el-table-column label="企业名称" prop="name"></el-table-column>
                    <el-table-column label="所在城市" prop="city"></el-table-column>
                    <el-table-column label="操作">
                        <template #default="o">
                            <el-button type="text" @click="audit(o.row.id)">审核通过</el-button>
                        </template>
                    </el-table-column>
<!--                    15:05-->
                </el-table>
                <el-pagination
                        background
                        layout="prev,pager,next"
                        :current-page.sync="page2"
                        :page-size.sync="pageSize2"
                        :total="total2"
                        @current-change="handleChange2"
                        style="margin-top: 30px"></el-pagination>
            </el-tab-pane>

        </el-tabs>
    </el-row>
</template>

<script>
    import BasicHeader from "@/components/BasicHeader"
    export default {
        name: "ShopManage",
        data: () => ({
            activeName:"audited",
            tableData1:[],
            page1:1,
            pageSize1:5,
            total1:0,
            search1:"",
            tableData2:[],
            page2:1,
            pageSize2:5,
            total2:0,
            search2:""
        }),
        methods: {
            fetchData1(){
                this.$axios.get(this.$api.SHOP,{page:this.page1,pageSize:this.pageSize1,search:this.search1,state:2,type:1}).then(res=>{
                    if(res.data.code===200){
                        this.tableData1=res.data.data;
                        this.total1=res.data.total;
                    }
                });
            },
            handleChange1(){
                this.fetchData1();
            },
            handleSearch1(){
                this.page1=1;
                this.fetchData1();
            },
            fetchData2(){
                this.$axios.get(this.$api.SHOP,{page:this.page2,pageSize:this.pageSize2,search:this.search2,state:1,type:1}).then(res=>{
                    if(res.data.code===200){
                        this.tableData2=res.data.data;
                        this.total2=res.data.total;
                    }
                });
            },
            handleChange2(){
                this.fetchData2();
            },
            handleSearch2(){
                this.page2=1;
                this.fetchData2();
            },
            audit(id){
                this.$confirm('确定将当前店铺审核状态设为通过吗？','提示',{
                    confirmButtonText:'确定',
                    cancelButtonText:'取消',
                    type:'warning'
                }).then(()=>{
                   this.$axios.put(this.$api.SHOP,{id,state:2}).then(res=>{
                       if(res.data.code===200){
                           this.activeName="audited";
                           this.fetchData1();
                           this.$message.success("操作成功");
                       }
                   })
                }).catch(()=>{
                    this.$message({
                        type:'info',
                        message:'已取消操作'
                    });
                });
            },
            quit(id){
                this.$confirm('确定通过当前店铺的注销申请吗？','提示',{
                    confirmButtonText:'确定',
                    cancelButtonText:'取消',
                    type:'warning'
                }).then(()=>{
                    this.$axios.put(this.$api.SHOP,{id,quit:3}).then(res=>{
                        if(res.data.code===200){
                            this.activeName="audited";
                            this.fetchData1();
                            this.$message.success("操作成功");
                        }
                    })
                }).catch(()=>{
                    this.$message({
                        type:'info',
                        message:'已取消操作'
                    });
                });
            },
            tableRowClassName({row,rowIndex}){
                if(row.quit===3){
                    return "delete"
                }
            }
        },
        components:{
          "basic-header":BasicHeader
        },
        mounted() {
            this.fetchData1();
            this.fetchData2();
        }
    }
</script>

<style scoped lang="scss">

</style>
<style>
    .el-table .delete{
        background:rgba(235,62,62,0.33)!important;
    }
</style>